<template><div><h1 id="java-基础语法" tabindex="-1"><a class="header-anchor" href="#java-基础语法"><span>Java 基础语法</span></a></h1>
<h2 id="基本语法规则" tabindex="-1"><a class="header-anchor" href="#基本语法规则"><span>基本语法规则</span></a></h2>
<h3 id="_1-大小写敏感" tabindex="-1"><a class="header-anchor" href="#_1-大小写敏感"><span>1. 大小写敏感</span></a></h3>
<p>Java 是大小写敏感的语言，例如 <code v-pre>Hello</code> 和 <code v-pre>hello</code> 是不同的标识符。</p>
<h3 id="_2-类名" tabindex="-1"><a class="header-anchor" href="#_2-类名"><span>2. 类名</span></a></h3>
<ul>
<li>类名应该以大写字母开头</li>
<li>如果类名包含多个单词，每个单词的首字母都应该大写</li>
<li>例如：<code v-pre>MyFirstJavaClass</code></li>
</ul>
<h3 id="_3-方法名" tabindex="-1"><a class="header-anchor" href="#_3-方法名"><span>3. 方法名</span></a></h3>
<ul>
<li>方法名应该以小写字母开头</li>
<li>如果方法名包含多个单词，第一个单词首字母小写，后面的单词首字母大写</li>
<li>例如：<code v-pre>calculateTotal</code></li>
</ul>
<h3 id="_4-源文件名" tabindex="-1"><a class="header-anchor" href="#_4-源文件名"><span>4. 源文件名</span></a></h3>
<ul>
<li>源文件名必须和类名相同</li>
<li>文件扩展名必须是 <code v-pre>.java</code></li>
<li>例如：<code v-pre>HelloWorld.java</code></li>
</ul>
<h3 id="_5-主方法入口" tabindex="-1"><a class="header-anchor" href="#_5-主方法入口"><span>5. 主方法入口</span></a></h3>
<p>所有的 Java 程序都由 <code v-pre>public static void main(String[] args)</code> 方法开始执行。</p>
